  margin-right: 12px;
}

.news-list__text {
  display: block;
  margin-bottom: 17px;
  line-height: 1.5;
  color: inherit;
  text-decoration: none;
}

.news-list__text:focus, .news-list__text:hover {
  color: #092f3b;
  outline: none;
}

.social-list {
  display: flex;
}

.social-list__item {
  margin-left: 5px;
}

.social-list__item:first-child {
  margin-left: 0;
}

.social-btn {
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-image: url(../img/ico/ico-fb.png);
}

.social-btn:hover {
  opacity: 0.8;
}

.social-btn--fb {
  background-image: url(../img/ico/ico-fb.png);
}

.social-btn--tw {
  background-image: url(../img/ico/ico-tw.png);
}

.tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-right: 5px;
}

.tags__term {
  margin-right: 10px;
}

.tags__descr {
  position: relative;
  margin-left: 1px;
}

.tags__descr:first-child {
  margin-left: 1px;
}

.tags__descr:last-child .tags__link::before {
  display: none;
}

.tags__link {
  position: relative;
  display: block;
  padding-right: 5px;
  color: #2b5e5e;
  font-style: italic;
  line-height: 1.5;
  text-decoration: underline;
}

.tags__link::before {
  content: ",";
  position: absolute;
  right: 2px;
  bottom: 0;
  color: #9c8d5f;
}

.tags__link:focus, .tags__link:hover {
  color: #ae0002;
  outline: none;
}

.header-page {
  margin-top: 33px;
}

@media only screen and (max-width: 999px) {
  .header-page {
    margin-top: 0;
  }
}

@media only screen and (max-width: 480px) {
  .header-page {
    display: flex;
    flex-direction: column;
  }
}

.header-page__top-panel > .container:first-child {
  border-radius: 4px 4px 0 0;
  background: linear-gradient(180deg, #ffffff, #e2e2e2);
}

.header-page__hot {
  padding: 11px 32px;
  background-color: #ededed;
  border-radius: 4px 4px 0 0;
  background: linear-gradient(180deg, #ffffff, #e2e2e2);
}

.header-page__main-panel {
  display: flex;
  padding-top: 29px;
  padding-right: 30px;
  padding-left: 30px;
  padding-bottom: 24px;
  background-color: #103948;
  background-image: url(../img/bg/bg-header.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  color: #ffffff;
}

@media only screen and (max-width: 999px) {
  .header-page__main-panel {
    justify-content: space-between;
  }
}

@media only screen and (max-width: 480px) {
  .header-page__main-panel {
    flex-direction: column;
    background-image: none;
    align-items: center;
    padding: 0;
  }
}

.header-page__logo {
  position: relative;
  margin-right: 29px;
  flex-shrink: 0;
  overflow: hidden;
}

@media only screen and (max-width: 999px) {
  .header-page__logo {
    margin-right: 20px;
  }
}

@media only screen and (max-width: 480px) {
  .header-page__logo {
    margin-right: 0;
    padding: 20px 10px;
  }
}

.header-page__menus {
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 999px) {
  .header-page__menus {
    flex-direction: row;
  }
}

@media only screen and (max-width: 480px) {
  .header-page__menus {
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    background-color: #fbfbfb;
  }
}

.header-page__open-btn {
  display: none;
  position: relative;
  border: 1px solid #730001;
  margin-left: 10px;
  padding: 6px;
  border-radius: 3px;
  color: #f6f6f6;
  background-color: #ae0002;
}

.header-page__open-btn:active .header-page__menu-line::before,
.header-page__open-btn:active .header-page__menu-line::after,
.header-page__open-btn:focus .header-page__menu-line::before,
.header-page__open-btn:focus .header-page__menu-line::after {
  width: 75%;
}

@media only screen and (max-width: 999px) {
  .header-page__open-btn {
    display: block;
  }
}

@media only screen and (max-width: 480px) {
  .header-page__open-btn {
    border: none;
  }
}

.header-page__mobil-close {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 5;
  padding: 1em;
  color: #a6a6a6;
}

.header-page__mobil-close .close-btn {
  border: 1px solid;
}

.header-page__mobil-close.is-active {
  display: block;
}

.header-page__menu-line {
  position: relative;
  display: inline-block;
  width: 27px;
  height: 2px;
  margin-bottom: 2px;
  vertical-align: middle;
  background: #f6f6f6;
}

.header-page__menu-line::before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
  width: 100%;
  height: 2px;
  background: #f6f6f6;
  transition: width 0.6s;
}

.header-page__menu-line::after {
  content: "";
  position: absolute;
  left: 0;
  top: -7px;
  width: 100%;
  height: 2px;
  background: #f6f6f6;
  transition: width 0.6s;
}

.services-menu {
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 767px) {
  .services-menu {
    flex-direction: column;
  }
}

.services-menu__link {
  display: inline-block;
  padding: 0 3px 8px 9px;
  color: #a6a6a6;
  font-family: barlowRegular, sans-serif;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.2s;
}

.services-menu__link:focus, .services-menu__link:hover {
  color: #f6f6f6;
  text-decoration: underline;
  outline: none;
}

@media only screen and (max-width: 480px) {
  .services-menu__link {
    padding: 3px;
    line-height: 1;
  }
  .services-menu__link:focus, .services-menu__link:hover {
    color: #092f3b;
  }
}

.all-news {
  display: flex;
  padding-top: 30px;
  padding-bottom: 36px;
}

@media only screen and (max-width: 999px) {
  .all-news {
    flex-direction: column;
  }
}

.all-news__banner-wrap {
  position: relative;
}

.all-news__full {
  position: relative;
  flex-grow: 1;
}

@media only screen and (min-width: 1000px) {
  .all-news__full {
    width: calc(100% - 215px);
  }
}

.all-news__aside {
  flex-shrink: 0;
  width: 187px;
  margin-left: 28px;
}

@media only screen and (max-width: 999px) {
  .all-news__aside {
    width: auto;
    margin-left: 0;
  }
}

.all-news__aside-title {
  margin-bottom: 5px;
}

.news-prev {
  margin-bottom: 43px;
  padding-top: 11px;
}

.news-prev__title {
  margin-bottom: 1.2em;
}

.news-prev__grid {
  display: flex;
  flex-wrap: wrap;
  margin-right: -7px;
  margin-left: -7px;
}

.news-prev__item {
  width: 25%;
  padding: 0 7px;
}

@media only screen and (max-width: 999px) {
  .news-prev__item {
    width: 50%;
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 767px) {
  .news-prev__item {
    width: 100%;
  }
}

.footer-page {
  position: relative;
  margin-top: auto;
  padding: 279px 0 80px;
}

.footer-page::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 48%, rgba(0, 0, 0, 0.15));
}

.footer-page__info > .container {
  background-color: transparent;
}
